<template>
  <header>
    <section id="top" class="top-wrapper">
      <h2> {{ msg }}</h2>
      <h1>{{ name }}</h1>
    </section>
  </header>
</template>

<script>
export default {
  name: 'MyHeader',
  data () {
    return {
      msg: 'Welcome to my portfolio site.',
      name: 'Kentaro Fujita'
    }
  }
}
</script>

<style lang="scss" scoped>
@import '@/assets/styles/_color.scss';
@import '@/assets/styles/_size.scss';

.top-wrapper {
  // margin-top: $header-height_pc;
  margin-top: 0;
  background: linear-gradient(to left, $main-color-1 0%, $main-color-2 100%);
  text-align: center;
  padding: 6vh 0;
}
h1 {
  // padding-top: 40vh;
  font-size: 60px;
  color: white;
  letter-spacing: 3px;
  font-weight: bold;
  border-bottom: 1px solid white;
  padding-right: 20px;
  padding-left: 20px;
  text-shadow: 0px 0px 30px rgba(0, 0, 0, 0.2);
}
h2 {
  color: white;
  font-weight: normal;
  font-size: 30px;
}
h3 {
  color: white;
  font-weight: normal;
  font-size: 25px;
  margin-top: 200px;
}
.share {
  position: absolute;
  bottom: 20px;
  left: 0px;
  right: 0px;
  display: block;
}
.button {
  width: 100px;
  height: 100px;
  background-color: white;
  border-radius: 100px;
  display: inline-block;
  margin: 20px;
  margin-top: 0px;
  transition: all 0.3s;
  &:hover {
    background-color: darken(white, 10%);
  }
}
/* タブレット */
@media (max-width: 1250px) {
  .top-wrapper {
    margin-top: $header-height_tablet;
    padding: 3vh 0;
  }
  h1 {
    font-size: 40px;
  }
}
/* スマートフォン */
@media (max-width: 670px) {
  .top-wrapper {
    padding: 3vh 0;
    margin-top: $header-height_sp;
    background: linear-gradient(
      to bottom,
      $main-color-1 0%,
      $main-color-2 100%
    );
  }
  .share {
    position: relative;
  }
  h1 {
    padding-top: 10vh;
    font-size: 30px;
  }
  h3 {
    margin-top: 100px;
    margin-bottom: 10px;
  }
  .button {
    width: 70px;
    height: 70px;
    border-radius: 70px;
  }
}
</style>



